.info__circle {
  @apply rounded-3xl shadow-lg bg-white z-10;
}

.info__box {
  @apply relative rounded-3xl bg-white overflow-hidden shadow-lg z-0;
}

.info__img {
  @apply w-full h-full p-8;
}

.info__frame {
  @apply px-6 py-4 mb-5;
}

.info__subtitles {
  @apply font-bold text-4xl text-gray-300;
}

.info__txt {
  @apply font-bold text-3xl text-gray-400 mt-6 mb-10;
}

.info__stats__titles {
  @apply flex justify-center font-bold text-3xl text-gray-400;
}

.info__stats {
  @apply font-bold text-2xl text-gray-500;
}

.info__name {
  @apply font-bold text-8xl mb-5 mt-6 text-gray-400;
}

.info__centered__box {
  height: 100%;
  grid-column-start: 1; /* Start from the first column */
  grid-column-end: 3;   /* End at the end of the first column */
  @apply mb-0;
}

.info__outter__frame {
  height: 80%;
}

.info__frame__grid {
  height: 100%;
  width: inherit;
  @apply grid grid-cols-3;
}
