.pokemon-button {
  height: inherit;
  aspect-ratio: 1;
  @apply rounded-full rounded-lg;
}

.red {
  @apply bg-red-500;
}

.blue {
  @apply bg-blue-600;
}

.red:hover {
  @apply bg-red-600;
}

.blue:hover {
  @apply bg-blue-500;
}

.red:active {
  @apply bg-red-700;
}

.blue:active {
  @apply bg-red-700;
}
